.itemMenu {
  width: calc(100% - 28px);
  height: 48px;
  padding: 0 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #d6d6d6;
  text-decoration: none;
  font-size: 15px !important;
  cursor: pointer;
  transition: 0.3s;
  border-left: 2px solid transparent;
  opacity: 1;
}

.iconButtCont {
  position: relative;
}

.iconButtCont .PopNotification {
  position: absolute;
  top: -10px;
  left: 5px;
}

.itemText {
  margin-left: 10px;
  font-size: 16px;
}

.indicatorCont {
  border-radius: 50%;
  height: 20px;
  width: 20px;
}

.indicator {
  background: #224f65;
  transform: scale(0);
  transition: 0.3s;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.indicatorSon {
  transition: 0.5s;
  height: 10px;
  width: 10px;
  background: linear-gradient(to bottom, #377fd7, #00d2ff);
  border-radius: 50%;
  transform: scale(0);
}

.itemMenu:hover .indicatorSon {
  transform: scale(0.5);
}

.itemMenu:hover .indicator {
  transform: scale(0.5);
}

.itemMenu:hover {
  opacity: 1;
}

.text {
  display: flex;
  align-items: center;
}

.closeModalButtonCont,
.closeModalButtonBack {
  height: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-template-rows: repeat(2, 1fr);
  width: 60px;
  position: absolute;
  right: 5px;
  top: 0px;
  cursor: pointer;
  /*color: gray;*/
  transition: 0.1s;
}

.closeModalButtonBack {
  left: 5px !important;
  right: inherit !important;
}

.closeModalButtonCont:hover {
  transform: scale(1.1);
}

.closeModalButtonBack:hover {
  transform: scale(1.1);
}

.closeModalButtonBack p {
  margin: 0 !important;
}

.closeModalButtonCont p {
  margin: 0 !important;
}

.activo {
