.ALbuttonActive .ALbuttonTextSpan {
  font-weight: bold;
  color: #1cb179 !important;
}

.ALbuttonActive > div {
  padding: 0 15px !important;
  font-size: 14px !important;
  height: 45px;
  background: linear-gradient(to bottom right, #fbfbfb, #c7c7c7) !important;
  color: gray;
  border: 2px solid #1cb179 !important;
}

.ALbuttonActive {
  animation-name: callToAction;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  margin: 0 !important;
  height: 50px;
  display: flex;
  align-items: center;
  /*border: 2px solid #ff764b;*/
  border-radius: 6px;
  transform-origin: center;
}

@keyframes callToAction {
  0% {
    transform: rotate(0deg);
  }

  79% {
    transform: rotate(0deg);
  }

  80% {
    transform: rotate(3deg);
  }

  85% {
    transform: rotate(-3deg);
  }

  90% {
    transform: rotate(3deg);
  }

  95% {
    transform: rotate(-3deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

@keyframes callToActionAtention {
  0% {
    transform: rotate(0deg);
  }

  18% {
    transform: rotate(1deg);
  }

  36% {
    transform: rotate(-1deg);
  }

  52% {
    transform: rotate(1deg);
  }

  70% {
    transform: rotate(-1deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

